select {
	/* Reset Select */
	appearance: none;
	outline: 0;
	border: 0;
	box-shadow: none;
	/* Personalize */
	flex: 1;
	padding: 0 1em;
	color: rgb(0, 0, 0);
	background-color: rgb(255, 255, 255);
	background-image: none;
	cursor: pointer;
	
  }
  /* Remove IE arrow */
  select::-ms-expand {
	display: none;
  }
  /* Custom Select wrapper */
  .select {
	box-shadow: 0 3px 1px -2px rgb(0 0 0 / 20%), 0 2px 2px 0 rgb(0 0 0 / 14%), 0 1px 5px 0 rgb(0 0 0 / 12%);
	position: relative;
	display: flex;
	height: 3em;
	border-radius: .25em;
	overflow: hidden;
  }
  /* Arrow */
  .select::after {
	content: '\25BC';
	position: absolute;
	top: 0;
	right: 0;
	padding: 1em;
	background-color: #ffffff;
	transition: .25s all ease;
	pointer-events: none;
  }
  /* Transition */
  .select:hover::after {
	color: #414040;
  }
  

